
.features-page {
  background-color: #000000; /* pure black */
  color: white; /* text visible on black */
  min-height: 100vh; /* full page height */
  padding: 2rem;
  font-family: "Inter var", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}



.features-title {
  font-size: 2.5rem;
  font-weight: 600px;
  color: white;
  margin-bottom: 10px;
  text-align: center;
}

.features-subtitle {
  font-size: 1.1rem;
  color: white;
  margin-bottom: 40px;
  justify-content: center;
}

.feature {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
  margin-bottom: 60px;
  text-align: left;
}

.feature:nth-child(even) {
  flex-direction: row-reverse; /* alternate layout */
}

.feature-img {
  width: 300px;
  height: auto;
  border-radius: 10px;
  object-fit: cover;
}

.feature-text {
  max-width: 500px;
}

.feature-text h3 {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 10px;
  color: whitesmoke;
}

.feature-text p {
  font-size: 1rem;
  color: white;
  line-height: 1.6;
}

/* Responsive Design */
@media (max-width: 768px) {
  .feature {
    flex-direction: column !important;
    text-align: center;
  }
  .feature-text {
    max-width: 90%;
  }
  .feature-img {
    width: 80%;
  }
}
